<template>
    <el-container>
        <el-header>
            <el-input type="text" v-model="serchkey" placeholder="请输入查询关键字"
                      style="min-width: 200px;max-width: 400px;  margin-right: 10px;" clearable
                      prefix-icon="el-icon-search"></el-input>
            <el-button type="primary" @click="queryUser()" icon="el-icon-search">查询</el-button>
            <el-button type="info" @click="addUser()" icon="el-icon-news">新增</el-button>
        </el-header>
        <el-main>
            <el-row>
                <el-col>
                    <el-table
                            :data="tableData"
                            border
                            stripe
                            style="width: 100%;">
                        <el-table-column
                                prop="name"
                                label="用户名称">
                        </el-table-column>
                        <el-table-column
                                prop="account"
                                label="用户账号">
                        </el-table-column>
                        <el-table-column
                                prop="nick"
                                label="用户昵称">
                        </el-table-column>
                        <el-table-column
                                prop="gender" label="性别">
                            <template slot-scope="scope">
                                <label>{{scope.row.gender=='0'?'男':'女'}}</label>
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="phone" label="手机号码">
                        </el-table-column>
                        <el-table-column
                                fixed="right"
                                label="操作"
                                width="185">
                            <template slot-scope="scope">
                                <el-button @click="editHandleClick(scope.row.userId,scope.row.account)" type="primary"
                                           size="mini" icon="el-icon-edit">
                                    编辑
                                </el-button>
                                <el-button @click="deleteHandleClick(scope.row.userId,scope.row.account)" type="danger"
                                           size="mini" icon="el-icon-delete">
                                    删除
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row>
                <el-pagination
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage"
                        :page-size="pageSize"
                        layout="total, prev, pager, next"
                        :total="countSize">
                </el-pagination>
            </el-row>
        </el-main>

        <el-dialog
                width="700px"
                :title="formTitle"
                :visible.sync="innerVisible"
                append-to-body>
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="用户账号" prop="account">
                    <el-input placeholder="请输入用户账号" v-model="ruleForm.account"></el-input>
                </el-form-item>
                <el-form-item label="用户密码" prop="password">
                    <el-input placeholder="请输入密码" v-model="ruleForm.password" show-password
                              autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="确认密码" prop="password1">
                    <el-input placeholder="请再次输入密码" v-model="ruleForm.password1" show-password
                              autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="用户名" prop="name">
                    <el-input placeholder="请输入用户名" v-model="ruleForm.name"></el-input>
                </el-form-item>
                <el-form-item label="用户组" prop="groups">
                    <el-select v-model="ruleForm.groups" style="width:100%" multiple placeholder="请选择用户组">
                        <el-option
                                v-for="item in options"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="用户昵称" prop="nick">
                    <el-input placeholder="请输入昵称" v-model="ruleForm.nick"></el-input>
                </el-form-item>
                <el-form-item label="性别" prop="gender">
                    <el-radio-group v-model="ruleForm.gender">
                        <el-radio label="0">男</el-radio>
                        <el-radio label="1">女</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="手机号码" prop="phone">
                    <el-input placeholder="请输入手机号码" maxlength="11" v-model="ruleForm.phone"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')" icon="el-icon-news">保存</el-button>
                    <el-button @click="resetForm('ruleForm')" icon="el-icon-refresh">重置</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </el-container>
</template>
<style>
    .el-table td {
        padding: 5px 0;
    }
</style>
<script>
    import md5 from 'js-md5';

    export default {
        data() {
            var validatePass = (rule, value, callback) => {
                if (!this.add) {
                    callback();
                }
                if (value === '') {
                    callback(new Error('请输入密码'));
                } else {
                    if (this.ruleForm.password1 !== '') {
                        this.$refs.ruleForm.validateField('password1');
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (!this.add) {
                    callback();
                }
                if (value === '') {
                    callback(new Error('请再次输入密码'));
                } else if (value !== this.ruleForm.password) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            };

            return {
                options: [],
                serchkey: '',
                tableData: null,
                innerVisible: false,
                innerOrgVisible: false,
                treeData: [],
                formTitle: '',
                orgNames: '',
                add: true,
                cloneUser: {},
                currentPage: 1,
                countSize: 0,
                pageSize: 1,
                ruleForm: {
                    userId: '',
                    account: '',
                    name: '',
                    password: '',
                    password1: '',
                    nick: '',
                    gender: '0',
                    phone: '',
                    type: '1',
                    groups: [],
                },
                defaultProps:
                    {
                        children: 'children',
                        label: 'name'
                    },
                rules: {
                    account: [
                        {required: true, message: '请输入账号', trigger: 'blur'},
                        {min: 3, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur'}
                    ],
                    password: [
                        {validator: validatePass, trigger: 'blur'}
                    ],
                    password1: [
                        {validator: validatePass2, trigger: 'blur'}
                    ],
                    name: [
                        {required: true, message: '请输入用户名', trigger: 'blur'},
                        {min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur'}
                    ],
                    nick: [
                        {min: 0, max: 10, message: '长度在 0 到 10 个字符', trigger: 'blur'}
                    ],
                    phone: [
                        {required: true, message: '请输入手机号码', trigger: 'blur'},
                        {min: 11, max: 11, message: '长度在11个字符', trigger: 'blur'}
                    ],
                    groups: [
                        {required: true, message: '请选择用户组', trigger: 'blur'},
                    ]
                }
            }
        },
        mounted() {
            this.reload();
            this.axios.get('/api/user/getAllGroup').then((response) => {
                this.options = response.data.data;
            }).catch((response) => {
                console.log(response);
            })
        },
        methods: {
            addUser() {
                this.innerVisible = true;
                this.formTitle = '新增用户'
                this.add = true;
                this.ruleForm.userId = '';
            },
            queryUser() {
                this.currentPage = 1;
                this.reload();
            },
            handleCurrentChange() {
                console.log('currentPage = ', this.currentPage)
                this.reload();
            },
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if (this.ruleForm.password) {
                            this.ruleForm.password = md5(this.ruleForm.password);
                            this.ruleForm.password1 = this.ruleForm.password;
                        }
                        this.axios.post('/api/user/save', {
                            ...this.ruleForm
                        }).then((response) => {
                            if (response.data.state == '0') {
                                this.$refs[formName].resetFields();
                                this.innerVisible = false;
                                //刷新数据
                                this.reload();
                            } else {
                                this.ruleForm.password = '';
                                this.ruleForm.password1 = '';
                                this.$message({
                                    type: 'info',
                                    message: response.data.message
                                });
                            }
                        }).catch((response) => {
                            console.log(response);
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
                if (!this.add) {
                    Object.assign(this.ruleForm, this.cloneUser);
                }
            },
            reload() {
                this.axios.get('/api/user/page?serchkey=' + this.serchkey + "&page=" + this.currentPage).then((response) => {
                    this.tableData = response.data.data.content;
                    this.currentPage = response.data.data.number + 1;
                    this.countSize = response.data.data.totalElements;
                    this.pageSize = response.data.data.size;
                }).catch((response) => {
                    console.log(response);
                })
            },
            editHandleClick(userId, account) {
                this.formTitle = '编辑用户【' + account + '】';
                this.innerVisible = true;
                this.add = false;
                this.ruleForm.userId = userId;
                this.cloneUser = {};
                this.init();
            },
            init() {
                this.axios.get('/api/user/get/' + this.ruleForm.userId).then((response) => {
                    this.ruleForm.userId = response.data.data.userId;
                    this.ruleForm.name = response.data.data.name;
                    this.ruleForm.account = response.data.data.account;
                    this.ruleForm.nick = response.data.data.nick;
                    this.ruleForm.phone = response.data.data.phone;
                    this.ruleForm.gender = response.data.data.gender;
                    this.ruleForm.groups = response.data.data.groups;
                    Object.assign(this.cloneUser, this.ruleForm);
                }).catch((response) => {
                    console.log(response);
                })
            },
            deleteHandleClick(userId, account) {
                this.$confirm('此操作将永久删除账号【' + account + '】, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.post('/api/user/delete/' + userId).then((response) => {
                        if (response.data.state == '0') {
                            //刷新数据
                            this.reload();
                        } else {
                            console.log(response);
                            this.$message({
                                type: 'error',
                                message: response.data.message
                            });
                        }
                    }).catch((response) => {
                        console.log(response);
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            }
        },
    }
</script>
